@value colors: '../../styles/colors.scss';@value elephant, white, porcelain from colors;.contentWrapper{list-style:none;margin:0;padding-left:0}.listItem{padding-bottom:20px}@media all and (min-width: 768px){.listItem{border-left:5px solid elephant;margin-left:20px;padding-bottom:40px}}.listItemTitle{align-items:center;display:flex;position:relative}.listItemTitle h3{margin:0}@media all and (min-width: 768px){.listItemTitle{margin-left:50px}}.listBullet{border-radius:50%;display:none}.listBullet.inner{background:elephant;border:white 10px solid;height:10px;margin:5px auto;width:10px}@media all and (min-width: 768px){.listBullet.inner{display:block}}.listBullet.outer{background:elephant;border:white 15px solid;height:40px;left:-87px;position:absolute;width:40px}@media all and (min-width: 768px){.listBullet.outer{display:inline-block}}.content{margin-left:0}@media all and (min-width: 768px){.content{margin-left:50px}}.extra{word-break:break-word}
