.mainContainer{position:absolute;top:0;left:0;right:0;bottom:0;z-index:100;background:rgba(0,0,0,0.75);display:flex;align-items:center;justify-content:center}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.loader{border:5px solid #f3f3f3;animation:spin 1s linear infinite;border-top:5px solid #555;border-radius:50%;width:50px;height:50px}
