@value colors: '../../styles/colors.scss';@value elephant, jungle-mist, porcelain from colors;.icon{margin:10px}.primary{color:elephant}.primary:hover{color:jungle-mist}.secondary{color:jungle-mist}.secondary:hover{color:elephant}.tertiary{color:porcelain}.tertiary:hover{color:jungle-mist}.animated{left:0;position:absolute;top:0}.animated.icon.default{transition:opacity 0.3s, transform 0.3s}.animated.icon.transform{opacity:0;transform:rotate(-180deg) scale(0.5);transition:opacity 0.3s, transform 0.3s}.animated.showAnimation.icon.default{opacity:0;transform:rotate(180deg) scale(0.5)}.animated.showAnimation.icon.transform{opacity:1;transform:rotate(0deg) scale(1)}
